import { useTheme } from '@emotion/react';
import {
    CAPITAL,
    gameModes,
    questionTypes,
} from 'data/config';
import * as React from 'react';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useGameOptions } from 'store/selector';
import { getGameSVG, pxToRem } from 'util/util';

import {
    Button,
    Grow,
    Stack,
    Typography,
    alpha,
} from '@mui/material';

import FunTypo from './ui/FunTypo';
import NewGameOptions from './NewGameOptions';

const NewGame = (props) => {
    const { cssTransition } = props;
    const theme = useTheme();
    const { t, i18n } = useTranslation();
    const gameOptions = useGameOptions();

    const [step, setStep] = useState(0);
    const [gameMode, setGameMode] = useState((gameOptions && gameOptions.gameMode) || CAPITAL);
    const [gameSubMode, setSubGameMode] = useState((gameOptions && gameOptions.gameMode) || CAPITAL);

    const handleClickMode = (event, gameModeKey) => {
        setGameMode(gameModeKey);
        setSubGameMode(gameModeKey);
        setStep(1);
    };
    const handleClickChangeMode = (event) => {
        setStep(0);
    };

    return (
        <>
            <Stack
                alignItems="center"
                direction="column"
                justifyContent="center"
                height="100%"
                flexGrow={1}
                gap={3}
                sx={{
                    ...cssTransition,
                }}
            >
                {step === 0 && (
                    <>
                        <Typography
                            variant="h2"
                            display="block"
                            width="100%"
                            fontSize={pxToRem(32)}
                            fontWeight="800"
                            textAlign="center"
                        >
                            <FunTypo
                                text={t('Game modes')}
                                color={theme.palette.text.main.replace('#', '')}
                                stroke={false}
                                strokeWidth={pxToRem(2)}
                                distance={pxToRem(3)}
                                sx={{
                                    fontSize: pxToRem(40),
                                    lineHeight: pxToRem(30),
                                    fontWeight: 800,
                                }}
                            />
                            {t('Game modes2') && (
                                <>
                                    <br />
                                    <FunTypo
                                        text={t('Game modes2')}
                                        color={theme.palette.text.main.replace('#', '')}
                                        stroke={false}
                                        strokeWidth={pxToRem(2)}
                                        distance={pxToRem(3)}
                                        sx={{
                                            fontSize: pxToRem(40),
                                            lineHeight: pxToRem(30),
                                            fontWeight: 800,
                                        }}
                                    />
                                </>
                            )}
                        </Typography>
                        <Stack
                            direction="row"
                            flexWrap="wrap"
                            justifyContent="center"
                            spacing={2}
                            useFlexGap
                            sx={{
                                maxWidth: '100%',
                                width: pxToRem(360),
                                px: pxToRem(24),
                            }}
                        >
                            {gameModes
                                .filter((option) => option.isMain)
                                .map((option, index) => {
                                    const questionType = questionTypes.find((qt) => option.questionType === qt.key);
                                    return (
                                        <Grow in={true} timeout={index * 100 + 200} key={option.key}>
                                            <Button
                                                variant="mode"
                                                size="large"
                                                disabled={
                                                    questionType !== undefined &&
                                                    questionType.disabledOffline &&
                                                    !navigator.onLine
                                                }
                                                sx={{
                                                    width: 'calc(50% - 12px)',
                                                    display: 'flex',
                                                    flexDirection: 'column',
                                                    alignItems: 'center',
                                                    py: pxToRem(32),

                                                    '&:hover': {
                                                        backgroundColor: theme.palette.background.tileHover,
                                                    },

                                                    svg: {
                                                        color: theme.palette.secondary.contrast,
                                                        height: pxToRem(64),
                                                        width: pxToRem(64),
                                                        fill: 'currentColor',
                                                        filter: `drop-shadow(2px 2px 0px ${alpha(theme.palette.shadow, 0.15)})`,
                                                    },
                                                }}
                                                onClick={(e) => handleClickMode(e, option.key)}
                                            >
                                                {getGameSVG(option.key)}
                                                <Typography
                                                    component="span"
                                                    fontSize={pxToRem(20)}
                                                    fontWeight="700"
                                                    mt={pxToRem(8)}
                                                    color="secondary.contrast"
                                                    sx={{
                                                        textShadow: `2px 2px 0px ${alpha(theme.palette.shadow, 0.1)}`,
                                                    }}
                                                >
                                                    {option.name[i18n.language]}
                                                </Typography>
                                            </Button>
                                        </Grow>
                                    );
                                })}
                        </Stack>
                    </>
                )}
                {step === 1 && (
					<NewGameOptions gameMode={gameMode} gameSubMode={gameSubMode} handleClickChangeMode={handleClickChangeMode} />
                )}
            </Stack>
        </>
    );
};

export default NewGame;
