import { useTheme } from '@emotion/react';
import { ReactComponent as GlobeIcon } from 'assets/images/globe.svg';
import useWindowDimensions from 'hooks/useWindowDimensions';
import { TOP_NAV_HEIGHT_REM } from 'layout/Header';
import * as React from 'react';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
// import GameFlag from './ui/GameFlag';
// import countriesData from 'data/countries.json';
import { setInWiki } from 'store/actions';
import { pxToRem } from 'util/util';

import ArrowCircleLeftIcon from '@mui/icons-material/ArrowCircleLeft';
import { Box, Stack, SvgIcon, Typography } from '@mui/material';

import NewGame from './NewGame';
import FunTypo from './ui/FunTypo';
import MainButton from './ui/MainButton';

const SCREEN_HOME = 0;
const SCREEN_GAME_OPTIONS = 1;

const Home = () => {
    const windowDimensions = useWindowDimensions();
    const [screen, setScreen] = useState(0);
    const theme = useTheme();
    const dispatch = useDispatch();
    const { t } = useTranslation();

    const handleClickNewGame = () => {
        setScreen(SCREEN_GAME_OPTIONS);
    };
    const handleClickWiki = () => {
        dispatch(setInWiki(true));
    };

    const handleClickHome = () => {
        setScreen(SCREEN_HOME);
    };

    const isLandscape = windowDimensions.width > windowDimensions.height && windowDimensions.height < 750;

    const showHome = screen === SCREEN_HOME;
    const showBigHomeLogo = showHome && !isLandscape;

    return (
        <>
            <Stack
                height="100%"
                flexGrow={1}
                position="relative"
                sx={{
                }}
            >
                <Box
                    position="absolute"
                    margin="0 auto"
                    width="100%"
                    top={showBigHomeLogo ? 0 : `calc(-${TOP_NAV_HEIGHT_REM}/2)`}
                    overflow="hidden"
                    sx={{
                        height: `calc(${showBigHomeLogo ? 30 : 20}vh - ${TOP_NAV_HEIGHT_REM})`,
                        minHeight: pxToRem(showBigHomeLogo ? 205 : 150),
                        transition: 'all 0.25s ease',
                        pointerEvents: 'none',
                    }}
                >
                    <Typography variant="h1" display="block">
                        <FunTypo
                            text={t('Country')}
                            color={theme.palette.text.title.replace('#', '')}
                            stroke={true}
                            strokeWidth={pxToRem(2)}
                            distance={pxToRem(6 * (showBigHomeLogo ? 1 : 0.7))}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(108 * (showBigHomeLogo ? 1 : 0.7)),
                                lineHeight: pxToRem(100 * (showBigHomeLogo ? 1 : 0.7)),
                                fontWeight: 800,
                                textAlign: 'center',
                                mb: pxToRem(-40 * (showBigHomeLogo ? 1 : 0.7)),
                                transition: '0.25s ease',
                            }}
                        />
                        <FunTypo
                            text={t('Quiz')}
                            color={theme.palette.text.title.replace('#', '')}
                            stroke={false}
                            strokeWidth={pxToRem(2)}
                            distance={pxToRem(7 * (showBigHomeLogo ? 1 : 0.7))}
                            sx={{
                                display: 'block',
                                fontSize: pxToRem(124 * (showBigHomeLogo ? 1 : 0.7)),
                                lineHeight: pxToRem(120 * (showBigHomeLogo ? 1 : 0.7)),
                                fontWeight: 800,
                                textAlign: 'center',
                                transition: '0.25s ease',
                                marginTop: pxToRem(12 * (showBigHomeLogo ? 1 : 0.7)),
                            }}
                        />
                    </Typography>
                </Box>
                {!isLandscape ? (
                    <>
                        {showHome && (
                            <Box width="100%" height="40vh" position="absolute" top="calc(50% - 120px)">
                                <SvgIcon
                                    sx={{
                                        height: pxToRem(240),
                                        width: pxToRem(240),
                                        display: 'block',
                                        margin: '0 auto',
                                        color:
                                            theme.palette.mode === 'dark'
                                                ? theme.palette.primary.light
                                                : theme.palette.secondary.main,
                                        opacity: theme.palette.mode === 'dark' ? 0.5 : 1,
                                    }}
                                >
                                    <GlobeIcon />
                                </SvgIcon>
                            </Box>
                        )}
                        {!showHome && (
                            <Box
                                display="flex"
                                alignItems="center"
                                flexDirection="column"
                                justifyContent="flex-start"
                                width="100%"
                                position="absolute"
                                sx={{
                                    height: '69vh',
                                    top: showHome
                                        ? 'calc(40vh - ' + TOP_NAV_HEIGHT_REM + ')'
                                        : 'calc(20vh - ' + TOP_NAV_HEIGHT_REM + ')',
                                }}
                            >
                                <NewGame />
                            </Box>
                        )}
                        <Box
                            display="flex"
                            alignItems="center"
                            flexDirection="column"
                            justifyContent={showHome ? 'center' : 'flex-start'}
                            width="100%"
                            position="absolute"
                            bottom="0"
                            sx={{
                                height: showHome ? '30vh' : '10vh',
                            }}
                        >
                            {showHome ? (
                                <Stack spacing={2}>
                                    <MainButton
                                        buttonP={{
                                            onClick: handleClickNewGame,
                                        }}
                                    >
                                        {t('Play')}
                                    </MainButton>
                                    <MainButton
                                        buttonP={{
                                            color: 'success',
                                            onClick: handleClickWiki,
                                        }}
                                    >
                                        {t('Wiki')}
                                    </MainButton>
                                </Stack>
                            ) : (
                                <MainButton
                                    buttonP={{
                                        color: 'success',
                                        onClick: handleClickHome,
                                        startIcon: <ArrowCircleLeftIcon />,
                                    }}
                                >
                                    {t('Home')}
                                </MainButton>
                            )}
                        </Box>
                    </>
                ) : (
                    <Box
                        display="flex"
                        alignItems="center"
                        flexDirection="column"
                        justifyContent="center"
                        width="100%"
                        height="65vh"
                        position="absolute"
                        bottom="0"
                    >
                        <Typography
                            variant="p"
                            display="block"
                            textAlign="center"
                            margin="0 auto"
                            maxWidth="400px"
                            sx={{
                                fontSize: pxToRem(32),
                                lineHeight: pxToRem(32),
                                fontWeight: 800,
                            }}
                        >
                            {t('Please use your device in portrait mode for an optimal experience')}
                        </Typography>
                    </Box>
                )}
            </Stack>
        </>
    );
};

export default Home;
